<template>
  <div class="hello">
  <group>
    <switch title="Default popup" :value.sync="show"></switch>
    <switch title="Full popup" :value.sync="show1"></switch>
    <switch title="Show cancel menu" :value.sync="showAction"></switch>
  </group>

  <actionsheet :show.sync="showAction" :menus="menusAction" @menu-click="click" show-cancel></actionsheet>

  <popup :show.sync="show">
    <div class="popup0">
      <group>
        <switch title="Another Switcher" :value.sync="show"></switch>
      </group>
    </div>
  </popup>
    <popup :show.sync="show1" height="100%">
    <div class="popup1">
      <group>
        <switch title="Another Switcher" :value.sync="show1"></switch>
      </group>
    </div>
  </popup>
  </div>

  <aside :show.sync="showLeft" placement="left" header="Title" :width="300">

  </aside>

  <button
    class="btn btn-danger btn-lg"
    @click="showLeft = true">Show Aside on left
  </button>

</template>

<script>
import { Popup, Group, Switch, Actionsheet } from 'vux'
import { aside } from 'vue-strap'

export default {
  name: 'Hello',
  components: {
    Popup,
    Group,
    Switch,
    aside,
    Actionsheet
  },
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      msg: 'Hello World!',
      // The layout mode, possible values are "grid" or "list".
      layout: 'grid',
      show: false,
      show1: false,
      showLeft: false,
      showAction: false,
      menusAction: {
        menu1: 'Take Photo',
        menu2: 'Choose from photos'
      }
    }
  }
}
</script>

